@import url(https://fonts.googleapis.com/css?family=Nunito+Sans&display=swap);


.header-component-style{display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:space-between;align-items:center;height:10%;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2)}.header-title{margin-left:50px;font-family:"Nunito Sans",sans-serif}.theme-button{margin-right:50px;color:black;font-family:"Nunito Sans",sans-serif;background-color:white;border:none;cursor:pointer}.theme-button:focus{outline:none}#regular-moon{display:inline}#solid-sun{display:none}.body-component-style{background-color:#fafafa;height:90%;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2)}.body-header{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-between;align-items:center}.countries-layout{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-evenly;align-items:center}body{margin:0}.search-box{font-family:"Nunito Sans",sans-serif;color:black;margin:35px 0px 0px 50px;box-sizing:border-box;border:1px none #ccc;box-shadow:0 0 2px rgba(0,0,0,0.2);border-radius:4px;width:350px;height:40px;-webkit-transition:width 0.5s ease-in-out;transition:width 0.5s ease-in-out;background-color:white;background-image:url("https://thechurchcouncil.org/wp-content/uploads/2016/06/transparent-magnifying-glass.png");background-position:15px 10px;background-repeat:no-repeat;background-size:20px;font-size:16px;padding:12px 20px 12px 50px}.search-box:focus{width:30%;outline:none}.dropdown-box{font-family:"Nunito Sans",sans-serif;font-size:16px;padding-left:10px;margin:35px 50px 0px 0px;width:150px;height:40px;background-color:white;border:none;border-radius:4px;box-shadow:0 0 2px rgba(0,0,0,0.2)}.dropdown-box:focus{outline:none}.card{width:300px;height:400px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2);border-radius:10px;margin-top:35px;margin-bottom:35px;cursor:pointer}.card-info{padding-left:20px;font-family:"Nunito Sans",sans-serif}.card-flag-image{width:300px;height:200px;object-fit:cover;border-top-right-radius:10px;border-top-left-radius:10px}

